@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
}

.first {
    height: 100vh;
}
.hola h1 {
  font-size: 1200%;
}

.hola {
  display: flex;
  justify-items: center;
  align-items: center;
  flex-direction: column;
  background: transparent;
}

.hey {
  text-align: center;
  position: relative;
  top: -5em;
  font-size: 1em;
  padding: 3em;
  padding-top: 0;
  padding-bottom: 0px;
}

.info {
  position: relative;
  top: 10em;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

@media screen and (max-width: 480px) {
  .hola h1 {
    font-size: 900%;
  }

  .info {
    text-align: center;
    position: relative;
    top: 2em;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
  }
}

@media screen and (max-height: 740px) {
  .hola h1 {
    font-size: 700%;
  }

  .info {
    text-align: center;
    position: relative;
    top: 2em;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
  }

  .hola {
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 14em;
  }
}
* {
    text-decoration: none;
    list-style: none;
}

.second {
    border-top: 2px solid rgb(196, 196, 196);
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    text-align: center;
}


.bgimg {
    width: 40%;
    transition: 0.15s ease-in-out;
}

.bgimg:hover {
    width: 41%;
}

.projects {
    font-family: monospace;
    font-size: 5rem;
    margin-bottom: 0;
}


.link {
    display: flex;
    color: rgb(0, 0, 0);
    align-items: center;
    transition: 0.1s ease-in-out;
    padding: 1em;
    border-radius: 0.3em;
}

.link .img {
    padding-right: 0.4em;
}


.link:hover {
    background-color: black;
    fill: rgb(255, 255, 255);
    color: white;
}

.list li {
    padding: 0.5em;
    transition: 0.1s ease;
}

.list li:hover {
    transform: scale(1.05);
}

@media screen and (max-width: 480px) {
    .projects {
        font-family: monospace;
        font-size: 4rem;
        margin-bottom: 0;
    }

    .list {
        padding-left: 0;
        margin-bottom: 0;
    }

}

@media screen and (max-height: 740px) {
    .list {
        font-size: 0.7em;
    }

    .list li {
        padding: 0.4em;
    }

    .bgimg {
        width: 75%;
        transition: 0.15s ease-in-out;
    }

    .well {
        padding-left: 1em;
        padding-right: 1em;
    }

    .projects {
        font-family: monospace;
        font-size: 3em;
        margin-bottom: 0;
        margin-top: 0.3em;
    }

    .second p {
        margin-top: 0;
    }
}
.about {
    margin-top: 1em;
    border-top: 2px solid rgb(196, 196, 196);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    padding: 3.5em;
    padding-bottom: 0px;
}

.social {
    margin-bottom: 1em;
}

.social svg {
    padding: 0.5em;
    margin-left: 3.5em;
    margin-right: 3.5em;
    width: 2%;
    transition: 0.2s ease;
    border-radius: 7px;
}

.social svg:hover {
    fill: white;
    background: black;
}

.footer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 10vh;
    border-top: 2px solid black;
    width: 100%;
}

.about h1 {
    font-size: 5em;
    font-family: monospace;
    margin-top: 0;
}

@media screen and (max-width: 480px) {
    .about {
        padding-top: 1em;
        padding-left: 1em;
        padding-right: 1em;
        font-size: 0.7em;

    }

    .about h1 {
        margin-bottom: 0;
    }

    .social {
        display: flex;
        justify-items: space-between;
        align-items: center;
    }

    .social svg {
        width: 2em;
        margin: 0;
    }
}


@media screen and (max-height: 740px) {
    .about h1 {
        font-size: 3em;
    }

    .about p,
    strong {
        font-size: 1.1em;
    }
}
.logo {
    width: 15%;
}

.outer {
    width: auto;
}

.dark {
    width: 3%;
    cursor: pointer;
}

.nav {
    margin-top: 0.5em;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 1.2em;
}

@media screen and (max-width: 480px) {
    .logo {
        width: 45%;
    }

    .dark {
        width: 9%;
    }

}
